import React from "react"
import PropTypes from "prop-types"
import { connect } from "react-redux"
import QueryBuilderSemantic from 'react-query-builder-semantic/QueryBuilderSemantic'

// we are importing some of the beautiful semantic UI react components
import {
  Segment,
  Input,
  Divider,
  Button,
  List,
  Grid,
  Icon,
  Label,
  Dropdown
} from "semantic-ui-react"

// here are our first two actions, we will be adding them in the next step, bear with me!
import {
  updateErddapInput,
  fetchErddapData,
  updateQuery,
} from "../actions/actions"

// some inline styles (we should move these to our index.css at one stage)
const segmentStyle = {
  zIndex: 999,
  position: "absolute",
  width: "400px",
  top: "10px",
  left: "10px",
  maxHeight: "calc(100vh - 5vw)",
  overflow: "auto",
  padding: "20px"
};

// some inline styles (we should move these to our index.css at one stage)
const segmentStyle2 = {
  zIndex: 999,
  position: "absolute",
  width: "800px",
  top: "10px",
  left: "500px",
  maxHeight: "calc(100vh - 5vw)",
  overflow: "auto",
  padding: "20px",
  marginTop: "0"
};

class Control extends React.Component {
  static propTypes = {
    erddapInput: PropTypes.string.isRequired,
    erddapResults: PropTypes.array.isRequired,
    isFetching: PropTypes.bool.isRequired,
  }

  constructor(props) {
    super(props)
    this.handleInputChange = this.handleInputChange.bind(this)
  }

  handleInputChange = event => {
    const { dispatch } = this.props;

    dispatch(
        updateErddapInput({
            inputValue: event.target.value
        })
    )
  }

  handleAddErddap = () => {
    const { dispatch, erddapInput } = this.props
    // If the text input has more then 0 characters..
    console.log(erddapInput)
    if (erddapInput.length > 0) {

      dispatch(
        fetchErddapData({
          inputValue: erddapInput
        })
      )
    }
  };

  handleErddapToggleVisibility = (name) => {
    const { dispatch, erddapResults } = this.props
    // handle button click
    console.log('Toggle Vis for ', name);
  }


  handleErddapRemoveDataset = (name) => {
    const { dispatch, erddapResults } = this.props
    // handle button click
    console.log('Remove Dataset ', name);
  }

  getErddapCommonFields = () => {
    const { erddapResults } = this.props
    let fields = [];
    erddapResults.forEach(x => {
      if(fields.length == 0){
        fields = x.attributes
      } else{
        fields = fields.filter(value => x.attributes.includes(value));
      }
    })
    fields = fields.map(x => {
      return {
        value:x,
        text:x
      };
    })
    return fields;
  }

  getErddapURLs = () => {
    const {erddapResults} = this.props
    if( erddapResults.length > 0){

      return erddapResults.map( x =>
        <List.Item>
          <List.Icon name='table' size='large' verticalAlign='middle' />
          <List.Content>

            <Button.Group floated='right'>
            <div data-tooltip="coming soon" data-position="top center">
              <Button small compact icon onClick={this.handleErddapToggleVisibility.bind(this, x.name)}>
                <Icon name='eye' />
              </Button>
              <Button small compact icon onClick={this.handleErddapToggleVisibility.bind(this, x.name)}>
                <Icon name='filter' />
              </Button>
              <Button small compact icon onClick={this.handleErddapRemoveDataset.bind(this, x.name)}>
                <Icon name='trash alternate' />
              </Button>
            </div>
            </Button.Group>

            <List.Header as={x.name}>{x.name}</List.Header>
            <List.Description as={x.name}></List.Description>
          </List.Content>
        </List.Item>
      )
    }
  }


  handleQueryChange  = json => {
    const { dispatch } = this.props
    console.log(json)
    dispatch(
      updateQuery({
        inputValue: json
      })
    )
  };

  render() {
    // The following constants are used in our search input which is also a semanticUI react component <Search... />
    const {
      isFetching,
      erddapInput,
      query
    } = this.props;

    return (
      <div>

        <Segment style={segmentStyle}>
          <div>
            <span>
              ERDDAP
            </span>
          </div>
          <Divider />

          <Grid>
            <Grid.Row columns={2}>
              <Grid.Column width={13}>
                <Input
                  compact
                  loading={isFetching}
                  value={erddapInput}
                  onChange={this.handleInputChange}
                  style={{ width: "100%" }}
                />
              </Grid.Column>
              <Grid.Column width={3}>
                <Button
                  icon
                  compact
                  onClick={this.handleAddErddap}
                >
                  <Icon
                    name='plus square'
                    size='large'
                    color='black'
                  />
                </Button>
              </Grid.Column>

            </Grid.Row>
            <Grid.Row columns={1}>
              <Grid.Column width={16}>
                <List divided relaxed >
                  {this.getErddapURLs()}
                </List>
              </Grid.Column>
            </Grid.Row>
          </Grid>
        </Segment>

        <Segment style={segmentStyle2}>
          <div>
            <span>
              Filters:
            </span>
            {/*<Label>
              station
              <span style={{ marginLeft:'12px' }}>=</span>
              <Label.Detail>QU29</Label.Detail>
            </Label>*/}
            <pre>{QueryBuilderSemantic.getQueryString(query)}</pre>
          </div>
          <Divider />
          <List>
            <List.Item>
              <List.Icon name='table' size='large' verticalAlign='middle' />
              <List.Content>
                <List.Header as='attribute'>station</List.Header>
                <List.Description as='attribute_description'>station</List.Description>
              </List.Content>
            </List.Item>
            <List.Item>
              <List.Icon name='table' size='large' verticalAlign='middle' />
              <List.Content>
                <List.Header as='attribute'>LCEWAP01</List.Header>
                <List.Description as='attribute_description'>Eastward Sea Water Velocity</List.Description>
              </List.Content>
            </List.Item>
            <List.Item>
              <List.Icon name='table' size='large' verticalAlign='middle' />
              <List.Content>
                <List.Header as='attribute'>LCNSAP01</List.Header>
                <List.Description as='attribute_description'>Northward Sea Water Velocity</List.Description>
              </List.Content>
            </List.Item>
          </List>
          <Divider />
          <QueryBuilderSemantic
            fields = {this.getErddapCommonFields()}
            operators = {[
                { value: 'is null', text: 'Is Null' },
                { value: 'is not null', text: 'Is Not Null' },
                { value: 'in', text: 'In' },
                { value: 'not in', text: 'Not In' },
                { value: '=', text: '=' },
                { value: '!=', text: '!=' },
                { value: '<', text: '<' },
                { value: '>', text: '>' },
                { value: '<=', text: '<=' },
                { value: '>=', text: '>=' },
            ]}
            query = {query}
            onQueryChange={this.handleQueryChange}/>
        </Segment>
      </div>
    )
  }
}

//
const mapStateToProps = state => {
  const erddapInput = state.isochronesControls.erddapInput
  const isFetching = state.isochronesControls.isFetching
  const erddapResults = state.isochronesControls.erddapResults
  const query = state.filters.query
  return {
    erddapInput,
    isFetching,
    erddapResults,
    query,
  }
}

export default connect(mapStateToProps)(Control)
